<style>
.m-tongji-loan {
  padding: 20px;
}
.m-tongji-loan-header {
  margin-bottom: 20px;
}
.m-tongji-loan .el-table td{
  padding: 5px 0;
}
.m-tongji-loan .el-input-group__prepend{
  width: 50px;
}
.m-tongji-loan-dialog .el-input {
  width: 300px;
}
.m-tongji-loan-dialog .el-form {
  padding: 20px;
}
</style>
<template>
  <div class="m-tongji-loan">
    <div class="search" style="margin: 15px 0;width: 300px;display:inline-block;">
      <el-input placeholder="请输入搜索内容" v-model="keyword" class="input-with-select">
        <el-select v-model="selected" slot="prepend" placeholder="请选择">
          <el-option label="姓名" value="name"></el-option>
          <el-option label="编号" value="customer_number"></el-option>
          <el-option label="手机" value="phone"></el-option>
        </el-select>
        <el-button @click="handleSearch" slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </div>
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :formatter="col.formatter"
        :width="col.width">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onEditEvent(scope.$index)"
            type="text">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="margin: 20px 0;text-align:center;"
      background
      :page-size="page_size"
      :total="total"
      layout="prev, pager, next"
      :current-page="page"
      @current-change="handlePageChange">
    </el-pagination>
    <el-dialog class="m-tongji-loan-dialog" append-to-body title="编辑客户信息" :visible.sync="showEditDialog">
      <el-form :model="item" ref="itemForm" label-position="right" label-width="120px">
        <el-form-item label="客户编号：">
          <span>{{ this.item.customer_number }}</span>
          <!-- <el-input v-model="item.customer_number" auto-complete="off" disabled></el-input> -->
        </el-form-item>
        <el-form-item label="姓名：">
          <span>{{ this.item.customer_name }}</span>
        </el-form-item>
        <el-form-item label="公司：">
          <span>{{ this.item.company }}</span>
        </el-form-item>
        <el-form-item label="业务员：">
          <span>{{ this.item.user_name }}</span>
        </el-form-item>
        <el-form-item label="合同编号：">
          <span>{{ this.item.contract_number }}</span>
        </el-form-item>
        <el-form-item label="借款金额：">
          <span>{{ this.item.loan_money | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="手续费：">
          <span>{{ this.item.procedure_fee | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="登记费：">
          <span>{{ this.item.mortgage_registration | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="其他费用：">
          <span>{{ this.item.other_fee | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="收款（本金）：">
          <span>{{ this.item.money | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="收款（利息）：">
          <span>{{ this.item.Interest | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="收款（其他）：">
          <span>{{ this.item.other_money | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="余额：">
          <span>{{ this.item.balance | currencyDisplay }}</span>
        </el-form-item>
        <el-form-item label="毛利率：">
          <span>{{ this.item.benfit_rate }}</span>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input type="textarea" :rows="3" placeholder="请输入内容" v-model="item.comment" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showEditDialog = false">取 消</el-button>
        <el-button type="primary" @click="onDialogComfirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import { sexDisplay, currencyDisplay} from '@/common/utils'

export default {
  data(){
    return {
      cols:[
        {
          prop: 'customer_number',
          label: '客户编号'
        },{
          prop: 'customer_name',
          label: '姓名',
          width: 80
        },{
          prop: 'company',
          label: '公司',
          width: 120
        },{
          prop: 'user_name',
          label: '业务员',
          width: 120
        },{
          prop: 'contract_number',
          label: '合同编号',
          width: 120
        },{
          prop: 'loan_money',
          label: '借款金额',
          width: 100,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },{
          prop: 'procedure_fee',
          label: '手续费',
          width: 100,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },{
          prop: 'mortgage_registration',
          label: '登记费',
          width: 100,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },{
          prop: 'other_fee',
          label: '其他费用',
          width: 100,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },
        {
          prop: 'money',
          label: '收款（本金）',
          width: 120,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },
        {
          prop: 'Interest',
          label: '收款（利息）',
          width: 120,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },
        {
          prop: 'other_money',
          label: '收款（其他）',
          width: 120,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },
        {
          prop: 'balance',
          label: '余额',
          width: 100,
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
        },
        {
          prop: 'benfit_rate',
          label: '毛利率',
          width: 100
        },
        {
          prop: 'loan_start',
          label: '借款开始日期',
          width: 120
        },
        {
          prop: 'loan_end',
          label: '借款结束日期',
          width: 120
        },
        {
          prop: 'loan_status',
          label: '借款状态',
          formatter:(r,c,v)=>{
            if(v){
              return '已到期'
            }else{
              return '未到期'
            }
          },
        },
        {
          prop: 'comment',
          label: '备注',
          width: 150
        }
      ],
      rows: [],
      loading: true,

      item: {},
      showEditDialog: false,
      
      page: 1,
      page_size: 10,
      total: 0,
      search: {
        name: '',
        customer_number: '',
        phone: ''
      },
      keyword: '',
      selected: null
    }
  },
  filters:{
    currencyDisplay
  },
  computed:{
    rows_inShow(){
      let arr = [];
      this.rows.forEach(item => {
        let obj = {}
        for(let key in item){
          if(key === 'car_status'){
            obj[key] = item[key]?'押车':'拖车';
          }else if(key === 'borrow_status'){
            obj[key] = item[key]?'已被借用':'可以借用';
          }else{
           obj[key] = item[key]
          }
        }
        arr.push(obj)
      })
      return arr;
    }
  },
  methods:{
    fetchList(data = {
      page: this.page,
      page_size: this.page_size,
      name: this.search.name,
      customer_number: this.search.customer_number,
      phone: this.search.phone
    }){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/loan_detail',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.rows = res.data.data;
        this.total = res.data.total;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    updateItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/loan_detail/comment',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '编辑放款明细表备注成功，请在列表中查看！',
          type: 'success'
        });
        this.fetchList();
        this.showEditDialog = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    handlePageChange(val){
      this.page = val;
      this.fetchList();
    },
    handleSearch(){
      if(!this.selected){
        this.$message('请先选择搜索类型！');
        return;
      }
      this.search[this.selected] = this.keyword;
      if(this.page == 1){
        this.fetchList();
      }else{
        this.page = 1;
      }
    },
    onEditEvent(index){
      this.item = this.rows[index];
      this.isEdit = true;
      this.showEditDialog = true;
      this.$refs['itemForm']?this.$refs['itemForm'].clearValidate():null;
    },
    onDialogComfirm(){
      this.updateItem({
        customer_number: this.item.customer_number,
        comment: this.item.comment
      });
    }
  },
  mounted(){
    this.fetchList();
  }
}
</script>




